/* Reset styles */
* {
  box-sizing: border-box;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  font-size: 29px;
}

/* body {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  color: black;
} */

body,
html {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

a {
  color: white;
  font-size: 40px;
  background-color: transparent;
  justify-content: space-around;
  text-decoration: none; /*Removes underline from links */
}

.nav-tabs {
  /* margin: 0;
  padding: 0;
  font-size: 20px; */
  list-style-type: none; /* Removes the bullet or any marker */
  margin-right: 7px;
  padding: 0 10px; /* Adds padding to the left and right of each link */
  background-color: transparent;
}

section {
  padding: 0px;
}

header {
  background-color: #000080;
  color: white;
  padding: 0px 3px;
  position: relative;
  top: 0;
  z-index: 1000;
}

.about-text {
  font-size: 20px;
}

