header nav ul li a:hover {
  color: rgb(155 200 221);
}

header h1 {
  margin: 0;
  font-size: 38px;
  color: black;
}

h2 {
  font-size: 44px;
  margin: 32px;
  background-color: transparent;
  color: white;
}

.contact-me {
  font-size: 20px;
  margin: 1px;
  background-color: transparent;
  color: black;
}

/* contact me styling starts */
/* .nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
} */
.nav {
  text-align: justify;
  text-decoration: none;
  /* padding-top: 0px; */
  padding-bottom: 2px;
  margin-bottom: 4px;
  display: flex;
  justify-content: flex-end;
}

a.nav-link {
  color: white;
  font-size: 20px;
  background-color: transparent; /* Ensures the background is transparent */
  text-decoration: underline; /* Removes underline from links */
  padding: 1px 0px;
}

.nav address a {
  padding: 5px;
  text-decoration: none;
  font-style: normal;
  color: black;
  font-size: 20px;
  /* border-bottom: 3px solid; */
  line-height: 1.5;
}

.nav address a:hover {
  color: blue;
}

.contact-section {
  background: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 20px;
  max-width: 600px;
  margin: auto;
}

.section-container {
  width: 100%;
  max-width: 969px;
  margin: 0 auto;
  padding: 20px;
  background: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  /* border-left: 5px solid black; */
  background-color: #f4f4f4;
}

textarea {
  height: 340px;
  width: 1670px;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 0px;
  border: 1px solid #ccc;
  border-radius: 14px;
  margin-bottom: 8px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  color: #333;
}
.button {
  width: 100%;
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}
.button:hover {
  background-color: #0056b3;
}

.error-message {
  color: red;
  padding: 5px 0;
}

.nav address a {
  display: block;
  margin-top: 5px;
  background-color: transparent;
}

.nav-item {
  padding-right: 20px; /* Adds space to the right of each nav item */
}

/* contact me styling ends */

