.hero {
  background-image: url("../src/assets/bgdisplay/Image-1.png");
  background-size: cover;
  height: 300px;
  display: flex;
  justify-content: end;
  align-items: flex-end;
  /* text-align: center; */
  color: white;
}

.box-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-around;
  align-items: stretch;
  width: 100%; /* Ensure full width */
  margin: 0 auto; /* Centering if needed */
  padding: 20px; /* Padding around the content inside the container */
  padding-left: 50px;
}

.section-container {
  max-width: none; /* Remove the max-width to allow full width */
  margin: auto; /* Previously was setting left and right margins. */
  padding: 20px;
  background: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.1);
  padding-left: 10px; /* Adjust this value as needed to move the title more to the left */
}

.box-wrapper {
  width: 100%; /* Full width for each project box */
}

.secondary-box {
  border: 1px solid #ccc; /* Subtle border for each project */
  padding: 20px; /* Increased padding for content */
  margin-bottom: 20px; /* Space between boxes */
}

.secondary-box img {
  width: 100%; /* Full width */
  height: auto; /* Maintain aspect ratio */
  margin-bottom: 10px; /* Space below the image */
}

.secondary-title h3 {
  margin-top: 0.5em; /* Space above the project title */
  font-size: 24px; /* Larger font size for titles */
}

.secondary-title p {
  font-size: 16px; /* Readable font size for descriptions */
  color: #333; /* Darker text color for better readability */
}

.section-title,
.download-link {
  margin-top: 0px;
  padding-top: 0px;
  background-color: transparent;
  color: black;
}

.section-border {
  padding-left: 0; /* Remove left padding if it's not aligning well */
  border-left: none; /* Remove left border if not required */
}

.section-border {
  /* determines how the sections are aligned properly */
  /* border-left: 5px solid; To add/icrease boldness border line "|" between <section-title> & <about-text> */
  margin: 1%;
  padding-left: 50px;
  padding-bottom: 20px;
  flex: 0 0 85%; /* adjust margin between <section-title> & <about-text> */
  left: 300px;
}

.work-header {
  margin-top: 0;
  padding-top: 0;
  align-self: flex-start;
}

.work {
  background-color: transparent; /* Sets the background to be transparent */
}

.welcome-section,
p {
  background-color: transparent;
  margin: 21px;
  padding: 0px;

  .section-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
  }

  .section-title {
    font-size: 32px;
    position: relative;
    padding-left: 20px;
    margin-bottom: 20px; /* Space below the title */
  }

  .section-title::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 5px;
    background-color: black; /* Match this to your site's theme */
  }

  .contact-details {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-top: 20px;
    margin-bottom: 20px; /* Space before the form */
  }

  .contact-details address {
    margin-right: 20px;
  }

  .contact-details address a {
    text-decoration: none;
    color: black;
    font-size: 20px;
    border-bottom: 2px solid transparent;
  }

  .contact-details address a:hover {
    border-bottom: 2px solid black;
  }

  .form-group {
    width: 100%;
  }

  .form-group input,
  .form-group textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
  }

  .form-group label {
    margin-bottom: 5px;
  }

  .button {
    width: 100%;
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
  }

  .button:hover {
    background-color: #0056b3;
  }

  .error-message {
    color: red;
    padding: 5px 0;
  }
}
/* footer styling starts */
.footer-section h1 {
  font-size: 15px;
}

.footer-section {
  display: flex;
  justify-content: center; /* Horizontally centers content within the footer */
  align-items: baseline; /* Aligns items along the baseline within the footer */
}
/* footer styling ends */

.resume-body {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Creates three columns */
  gap: 20px; /* Sets the gap between columns */
  padding: 20px; /* Adds padding around the grid */
}

.download-link {
  color: black; /* or any other contrasting color */
  text-decoration: none; /* Optional: removes underline */
}

.article {
  color: black;
  background-color: white; /* Darker green for each column */
  padding: 10px;
  border-radius: 10px; /* Rounded corners for the boxes */
}

.column-title {
  font-size: 24px; /* Larger text for titles */
  color: #333; /* Dark text color */
  margin-bottom: 10px; /* Space below the title */
}

.column-text ul {
  list-style-type: none; /* Removes bullet points */
  padding: 0;
}

.column-text li {
  margin-bottom: 5px; /* Space between items */
  font-size: 16px; /* Smaller text for items */
  list-style-type: none; /* Removes bullet points */
}

.column-text .logo {
  margin-right: 5px; /* Space next to icons */
  color: #4caf50; /* Icon color */
}

@media screen and (max-width: 768px) {
  header {
    flex-direction: column;
    padding: 15px;
    padding-left: 10px;
  }
  .section-title {
    margin-top: -10px; /* Less negative margin on smaller screens */
  }
}

@media (max-width: 600px) {
  .form-group {
    width: 100%;
  }
}
